import Nos from '../views/Nos';
import Inicial from '../views/Inicial';
import Contato from '../views/Contato';
import Processo from '../views/Processo';
import Servicos from '../views/Servicos';

const prefix = process.env.NODE_ENV === 'production' ? '/gohost' : '';

const appRoutes = [
    {
        path: `${prefix}/inicial`,
        component: Inicial,
        sidebarName: 'Inicial'
    },
    {
        path: `${prefix}/nos`,
        component: Nos,
        sidebarName: 'Nós'
    },
    {
        path: `${prefix}/processo`,
        component: Processo,
        sidebarName: 'Processo'
    },
    {
        path: `${prefix}/servicos`,
        component: Servicos,
        sidebarName: 'Serviços e Pacotes'
    },
    {
        path: `${prefix}/contato`,
        component: Contato,
        sidebarName: 'Contato'
    },
    {
        path: '/',
        redirect: true,
        to: `${prefix}/inicial`
    }
];

export default appRoutes;